.captionBlock {

    margin-bottom: 52px;
    text-align: center;
}

.container {
    display: flex;
    flex-direction: column;
    flex: 0 0 66.666667%;
    max-width: 66.666667%;
    margin: 0 auto;
}

.captionBlock span {
    font-size: 16px;
    font-weight: 600;
    color: #d1d1d1;
    line-height: 26px;
    display: block;
    text-align: center;
    margin-bottom: 0;
}

.captionBlock h2 {
    margin-top: 0;
    margin-bottom: 23px;
    font-size: 32px;
    line-height: 38px;
    font-weight: 700;
    color: #fff;
}

.captionBlock p {
    display: block;
    margin: 0 auto;
    color: rgba(208, 208, 208, 0.9);
    font-size: 16px;
    line-height: 1.625;
    hyphens: auto;
    font-family: "Open Sans", sans-serif;
}